import React from 'react';
import TableLine from './TableLine'

class Table extends React.Component {

    constructor() {
        super();
        this.state = {
            isLoaded: false,
            json: {},
        }
    }

    componentDidMount() {
        fetch("https://api.exchangeratesapi.io/latest?base=RUB")
            .then(response => response.json())
            .then(result => this.setState({
                isLoaded: true,
                json: result,
            }));
    }

    render() {
        const isLoaded = this.state.isLoaded;
        if (!isLoaded)
            return (<></>)
        else {
            const values = this.state.json.rates
            return (
                <table className="Table" style={{display: "flex", justifyContent: "space-between", border: "2px solid #a3a8a6",width: "155px", borderRadius: "10px", margin:"auto"}}>
                    <tbody>
                        <TableLine currencyId="USD" rubCost={values.USD}  />
                        <TableLine currencyId="EUR" rubCost={values.EUR}  />
                        <TableLine currencyId="GBP" rubCost={values.GBP}  />
                        <TableLine currencyId="CAD" rubCost={values.CAD}  />
                        <TableLine currencyId="CZK" rubCost={values.CZK}  />
                        <TableLine currencyId="JPY" rubCost={values.JPY}  />
                    </tbody>
                </table>
            );
        }
    }
}
export default Table