import React from "react";
import img from "./logo.png";
import {Link} from "react-router-dom";
import fire from './Fire';

class Header extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
        authUser: null,
    };
}

  componentDidMount() {
    this.listener = fire.auth().onAuthStateChanged(authUser => {
        authUser
        ? this.setState({ authUser })
        : this.setState({ authUser: null })
    });
}

componentWillUnmount() {
  this.listener();
}

  render(){
    return(
      <div class="Head" style={{display: "flex", justifyContent: "space-between", width:  "60%", border: "2px solid #a3a8a6", borderRadius: "10px", margin: "1em auto 1em"}}>
        <div class="logo" style={{display: "flex"}}>
            <img src={img} style={{height: "50px", width: "50px", margin:"auto"}}/>
                <p class="text">
                  <Link to="/lab5">Курс валют</Link></p>
        </div>
        <div class="login" style={{marginRight:"5px",marginTop:"auto",marginBottom:"auto", display: "flex", flexDirection: "column"}}>
            {this.state.authUser ? <AuthButtons /> : <NonAuthButtons />}
        </div>
        </div>
    )
  }
}

const AuthButtons = () => (
  <>
      <div className="Upper-button">
          <Link className="Link" to="/lab5/converter">Конвентер</Link>
      </div>
      <div className="Lower-button" onClick={() => fire.auth().signOut()} style={{color:"#541c89", cursor:"pointer", textDecoration:"underline"}}>
          Выйти
      </div>
  </>
);

const NonAuthButtons = () => (
  <>
      <div className="Upper-button">
          <Link className="Link" to="/lab5/login">Войти</Link>
      </div>
      <div className="Lower-button">
          <Link className="Link" to="/lab5/signup">Зарегистрироваться</Link>
      </div>
  </>
);


export default Header;