import React, { Component } from 'react';
import { Link,withRouter } from 'react-router-dom';
import fire from './Fire';


class SignupBase extends Component {
  constructor(props) {
    super(props);
    this.login = this.login.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.signup = this.signup.bind(this);
    this.state = {
      email: '',
      password: '',
      user: ''
    };
  }

  handleChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  login(e) {
    e.preventDefault();
    fire.auth().signInWithEmailAndPassword(this.state.email, this.state.password).then((u)=>{
    }).catch((error) => {
        console.log(error);
      });
  }

  signup(e){
    e.preventDefault();
    fire.auth().createUserWithEmailAndPassword(this.state.email, this.state.password).then((u)=>{
      this.props.history.push("/lab5");
    }).then((u)=>{console.log(u)})
    .catch((error) => {
        console.log(error);
      });
  }


  render() {
    return (
       <div className="col-md-6" style={{display: "flex", justifyContent: "center", border: "2px solid #a3a8a6",width: "145px", borderRadius: "10px", margin:"auto"}}>
       <form>
       <div class="form-group">
       <label for="exampleInputEmail1">User name</label>
       <input value={this.state.user} onChange={this.handleChange} type="user" name="user" class="form-control" id="exampleInputUser"  placeholder="Enter user name" />
        </div>
      <div class="form-group">
       <label for="exampleInputEmail1">Email address</label>
       <input value={this.state.email} onChange={this.handleChange} type="email" name="email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter email" />
        </div>
       <div class="form-group">
      <label for="exampleInputPassword1">Password</label>
      <input value={this.state.password} onChange={this.handleChange} type="password" name="password" class="form-control" id="exampleInputPassword1" placeholder="Password" />
      </div>
      <button onClick={this.signup} className="btn btn-success" style={{display: "flex", justifyContent: "center", margin:"auto"}}>Signup</button>
 </form>
 
 </div>
    );
  }
}

const Signup=withRouter(SignupBase);
export default Signup;