import React, {useEffect, useState} from 'react';
import CurrencyRow from './CurrencyRow';

const api_url ='https://api.exchangeratesapi.io/latest'

function Converter(){

    const [currencyOptions, setCurrencyOptions] = useState([])
    const [fromCurrency, setFromCurrency] = useState()
    const [toCurrency, setToCurrency] = useState()
    const [exchangeRate, setExchangeRate] = useState()
    const [amount, setAmount] = useState(1)
    const [amountInFromCurrency, setAmountInFromCurrency] = useState(true)

    let toAmount, fromAmount
    if (amountInFromCurrency) {
        fromAmount = amount
        toAmount = amount * exchangeRate
    } else {
        toAmount = amount
        fromAmount = amount / exchangeRate
    }

    useEffect(() => {
        fetch(api_url)
        .then(res => res.json())
        .then(data => {
            const firstCurrency = Object.keys(data.rates)[0]
            setCurrencyOptions([data.base, ...Object.keys(data.rates)])
            setFromCurrency(data.base)
            setToCurrency(firstCurrency)
            setExchangeRate(data.rates[firstCurrency])
        })
    }, [])

    useEffect(() => {
        if(fromCurrency != null && toCurrency != null){
        fetch(`${api_url}?base=${fromCurrency}&symbols=${toCurrency}`)
        .then(res => res.json())
        .then(data => setExchangeRate(data.rates[toCurrency]))
        }
    })

    function handleFromAmountChange(e) {
        setAmount(e.target.value)
        setAmountInFromCurrency(true)
    }

    function handleToAmountChange(e) {
        setAmount(e.target.value)
        setAmountInFromCurrency(false)
    }
    
    return(
        <div style={{display:"flex",flexDirection:"column", justifyContent:"center",marginLeft: "auto", marginRight:"auto",width: "250px", border: "2px solid #a3a8a6", borderRadius: "10px",paddingBottom:"5px"}}>
        <h1 style={{display:"dlex", justifyContent: "center", margin:"auto"}}>Конвентер</h1>
        <CurrencyRow 
        currencyOptions = {currencyOptions}
        selectedCurrency = {fromCurrency}
        onChangeCurrency = {e => setFromCurrency(e.target.value)}
        onChangeAmount={handleFromAmountChange}
        amount = {fromAmount}
        />
        <div style={{display:"flex", justifyContent:"center",fontSize:"150%"}}>=</div>
        <CurrencyRow 
        currencyOptions = {currencyOptions}
        selectedCurrency = {toCurrency}
        onChangeCurrency = {e => setToCurrency(e.target.value)}
        onChangeAmount={handleToAmountChange}
        amount = {toAmount}
        />
        </div>
    );
}

export default Converter;